<template>
  <div :class="classes">
    <div :class="oprClasses">
      <CaseSearch @on-search="handleSearch"></CaseSearch>
      <span :class="totalClasses">{{total}}条</span>
      <iButton @click="handleCreate" size="small">新建</iButton>
    </div>
    <DataList :keyword="keyword"  v-on:listenTotal="listenTotal"></DataList>
    <!-- <AppModal ref="modal" @on-close="handleClose" v-show="showCreateModal"></AppModal> -->
  </div>
</template>

<script>
  import CaseSearch from '@/views/cases/CaseSearch'
  import iButton from 'iview/src/components/button'
  import DataList from './DataList.vue'
  // import AppModal from './AppModal.vue'

  const prefix = 'gtu-console-mydata'

  export default {
    components: {
      CaseSearch,
      DataList,
      iButton,
      // AppModal
      
    },
    data () {
      return {
        keyword: '',
        showCreateModal: false,
        total: 3
      }
    },
    computed: {
      classes () {
        return [`${prefix}`, 'gtu-container']
      },
      oprClasses () {
        return `${prefix}-opr`
      },
      totalClasses () {
        return `${prefix}-total`
      }
    },
    methods: {
      handleSearch (keyword) {
        this.keyword = keyword
        var $this = this
        setTimeout(function () {
          $this.$refs.childMethod.getAppList()
        }, 1000)
      },
      handleCreate (event) {
        //To do
        this.showCreateModal = true
        var modal = this.$refs.modal
        modal.showModal = true
      },
      handleClose () {
        this.showCreateModal = false
      },
      listenTotal (total) {
        this.total = total
      }
    }
  }
</script>

<style lang="scss">
  $prefix: ".gtu-console-mydata";
  #{$prefix} {
    padding: 80px 0;
    &-opr {
      position: relative;
      border-bottom: 1px solid #dddee1;
      &::after {
        content: ' ';
        display: table;
        clear: both;
      }
      #{$prefix}-total {
        margin-left: 200px;
        font-size: 14px;
      }
      .gtu-cases-search {
        right: auto;
        left: 0;
        bottom: -1px;
      }
      .ivu-btn {
        float: right;
        padding: 2px 15px;
        margin-bottom: 3px;
        margin-right: 10px;
      }
    }
  }
</style>


