<template>
  <div>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="4" offset="2">搜索框</iCol>
        <iCol span="6">
          <iSwitch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
          </iSwitch>
        </iCol>
        <iCol span="4" offset="2">图例</iCol>
        <iCol span="6">
          <iSwitch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
          </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="4" offset="2">标位</iCol>
        <iCol span="6">
          <iSwitch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
          </iSwitch>
        </iCol>
        <iCol span="4" offset="2">标题</iCol>
        <iCol span="6">
          <iSwitch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
          </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="4" offset="2">级别限制</iCol>
        <iCol span="18">
          <iSwitch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
          </iSwitch>
        </iCol>
        
    </iRow>
    
  </div>
</template>

<script>
  import iRow from 'iview/src/components/Row'
  import iCol from 'iview/src/components/Col'
  import iSwitch from 'iview/src/components/Switch'

  const prefix = 'gtu-component-index'
  
  export default {
    components: {
      iRow,
      iCol,
      iSwitch
    },
    name: 'component-index',
    data () {
      return {
        sliderCategory: ''
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {

    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-component-index';
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-size: 100%;
  }
</style>


