<template>
  <div>
    <Map2d :class="classes"></Map2d>
    <Sidebar width="70px" @on-change-slider="changeSliderContent"></Sidebar>
    <Slider right="70px" :sliderCategory="sliderCategory" :showSlider="showSlider" @on-close="handleClose"></Slider>
    <UserOpr></UserOpr>
  </div>
</template>


<script>
  import Sidebar from './Sidebar'
  import UserOpr from './UserOpr'
  import Slider from './Slider'

  const prefix = 'gtu-console-main'
  
  export default {
    components: {
      Map2d: () => import(/* webpackChunkName:common */'@/components/map2d'),
      Sidebar,
      Slider,
      UserOpr
    },
    name: 'console_main',
    data () {
      return {
        sliderCategory: 'data',
        showSlider: false
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {
      changeSliderContent(name) {
        // name: 菜单项的id值
        this.sliderCategory = name;
        this.showSlider = true;
      },
      handleClose () {
        this.showSlider = false;
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-console-main';
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    background: #ADBDCC url('/static/images/console_main_bg.jpeg');
    width: 100%;
    background-size: 100%;
  }
</style>


