<template>
  <div :class="classes">
    <Dropdown placement="right" @on-click="handleClick">
      <a href="javascript:void(0)"><span :class="photoClasses"><Icon type="person"></Icon></span></a>
      <DropdownMenu slot="list">
          <DropdownItem name="add"><Icon type="fork-repo"></Icon>新建应用</DropdownItem>
          <DropdownItem name="apps"><Icon type="ios-paper"></Icon>应用管理</DropdownItem>
          <DropdownItem name="doc"><Icon type="ios-lightbulb"></Icon>帮助</DropdownItem>
          <DropdownItem name="userInfo"><Icon type="android-person"></Icon>个人中心</DropdownItem>
      </DropdownMenu>
    </Dropdown>
  </div>
</template>

<script>
  import {docsUrl} from '@/utils/staticUrl'
  import Icon from 'iview/src/components/icon'
  import Dropdown from 'iview/src/components/dropdown'

  const prefix = 'gtu-console-main-useropr'

  export default {
    components: {
      Icon,
      Dropdown,
      DropdownMenu: Dropdown.Menu,
      DropdownItem: Dropdown.Item
    },
    computed: {
      classes () {
        return `${prefix}`
      },
      photoClasses () {
        return `${prefix}-photo`
      }
    },
    methods: {
      handleClick (name) {
        if (name == 'doc') {
          window.open(`${docsUrl}`, '_doc')
        }
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-console-main-useropr';
  $photoSize: 40px;
  #{$prefix} {
    position: absolute;
    top: 100px;
    left: 100px;
    z-index: 999;
    .ivu-dropdown-rel {
      a {
        display: block;
        line-height: 0;
      }
    }
    &-photo {
      display: inline-block;
      width: $photoSize;
      height: $photoSize;
      line-height: $photoSize;
      border-radius: 50%;
      vertical-align: middle;
      background: #000;
      text-align: center;
      font-size: $photoSize - 10;
      color: #ccc;
      margin-right: 5px;
    }
    .ivu-select-dropdown {
      padding-left: 5px;
      padding-right: 5px;
    }
    .ivu-dropdown-menu {
      display: flex;
    }
    .ivu-dropdown-item {
      text-align: center;
      .ivu-icon {
        display: block;
        font-size: 15px;
      }
    }
  }
</style>


