<template>
  <Modal v-model="showModal" width="600" class="gtu-modal" @on-cancel="handleClose">
    <p slot="header">新建地图</p>
    <iForm :model="fromCreated" ref="fromCreated">
      <FormItem prop="mapName">
        <iInput v-model="fromCreated.mapName" placeholder="地图名称"></iInput>
      </FormItem>
      <FormItem>
        <iButton icon="ios-plus-empty" type="dashed">添加标签</iButton>
      </FormItem>
      <FormItem prop="mapDesc">
        <iInput v-model="fromCreated.mapDesc" :autosize="{minRows: 4,maxRows: 7}" type="textarea" placeholder="关于地图"></iInput>
      </FormItem>
      <FormItem label="创建类型" prop="mapType">
        <RadioGroup v-model="fromCreated.mapType">
          <iRadio label="2d">二维应用</iRadio>
          <iRadio label="3d">三维应用</iRadio>
        </RadioGroup>
      </FormItem>
    </iForm>
    <div slot="footer">
      <iButton :loading="modalLoading" @click="handleCreate">创建</iButton>
    </div>
  </Modal>
</template>

<script>
  import Modal from 'iview/src/components/modal'
  import iButton from 'iview/src/components/button'
  import iForm from 'iview/src/components/form'
  import iInput from 'iview/src/components/input'
  import iRadio from 'iview/src/components/radio'

  export default {
    components: {
      Modal,
      iButton,
      iForm,
      FormItem: iForm.Item,
      iInput,
      iRadio,
      RadioGroup: iRadio.Group
    },
    data () {
      return {
        showModal: true,
        modalLoading: false,
        fromCreated: {
          mapName: '',
          mapDesc: '',
          mapType: '2d'
        }
      }
    },
    methods: {
      handleCreate () {
        this.modalLoading = true
        // alert('创建')
        var self = this
        setTimeout(function () {
          self.handleReset()
          self.modalLoading = false
          self.showModal = false
        }, 1000)
      },
      handleClose () {
        this.handleReset()
        this.$emit('on-close')
      },
      handleReset () {
        this.$refs.fromCreated.resetFields()
      }
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-modal';
  $modal-color: #383838;
  #{$prefix}{
    .ivu-modal {
      border: 1px solid $modal-color;
    }
    .ivu-modal-content {
      border-radius: 0;
    }
    .ivu-modal-close {
      top: 4px;
    }
    .ivu-modal-close .ivu-icon-ios-close-empty:hover {
      color: #fff;
    }
    .ivu-modal-header {
      padding: 8px 16px;
      border-bottom: 0;
      background: $modal-color;
      p{
        color: #fff;
        font: {
          size: 14px;
          weight: 400;
        }
      }
    }
    .ivu-modal-footer {
      padding: 0px 16px 25px;
      border-top: 0;
      text-align: center;
      .ivu-btn {
        width: 40%;
        background: $modal-color;
        color: #fff;
      }
    }
    .ivu-modal-content {
      .ivu-form-item {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        .ivu-btn {
          padding: 3px 12px;
          color: #57a3f3;
        }
      }
    }
  }
</style>


