<template>
  <div>
    <br>
    <iRow type="flex" justify="center" align="bottom">
       <iCol span="21" offset="3">
          <img src="/pub/shares/qr/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL" alt="qrcode">
          <p style="margin-left:75px">点击二维码浏览</p>
       </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
      <iCol span="20" offset="4">
        <my-share :url="url" ></my-share>
      </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
       <iCol span="21" offset="3">
         <p>分享链接</p>
       </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
       <iCol span="14" offset="5">
        <p><iInput v-model="address"  readonly icon="ios-copy-outline"  ></iInput></p>
       </iCol>
    </iRow>
<br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="4" offset="3">图层列表</iCol>
        <iCol span="14" offset="3">
          <iSwitch size="large">
              <span slot="open">ON</span>
              <span slot="close">OFF</span>
          </iSwitch>
        </iCol>
    </iRow>
  </div>
</template>

<script>
  import iRow from 'iview/src/components/Row'
  import iCol from 'iview/src/components/Col'
  import iSwitch from 'iview/src/components/Switch'
  import iInput from 'iview/src/components/input'

  const prefix = 'gtu-component-index'
  
  export default {
    components: {
      iRow,
      iCol,
      iSwitch,
      iInput
    },
    name: 'component-index',
    data () {
      return {
        address: '',
        url: '',
        src: ''
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {

    },
    mounted () {
      this.url = '/apps/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL/shares?ak=2987f7fa516c1888f710306686b44270d005e0443429067b8ae16c1076bce1b4'
      this.src = '/pub/shares/qr/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL'
      this.address = this.url
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-share-index';
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-size: 100%;
  }
</style>


