<template>
  <div>
    <br>
    <iRow type="flex" justify="center" align="bottom">
      <iCol span="16" offset="8">
        <iButton type="primary" icon="plus" size="large" >添加数据</iButton>
      </iCol>
    </iRow>
  </div>
</template>

<script>
  import iRow from 'iview/src/components/Row'
  import iCol from 'iview/src/components/Col'
  import iSwitch from 'iview/src/components/Switch'
  import iButton from 'iview/src/components/button'

  const prefix = 'gtu-map-data'
  
  export default {
    components: {
      iRow,
      iCol,
      iSwitch,
      iButton
    },
    name: 'map-data',
    data () {
      return {
        sliderCategory: ''
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {

    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-map-data';
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-size: 100%;
  }
</style>


