<template>
  <div :class="classes">
    <iMenu theme="dark" active-name="1" :width="width" @on-select="handleMenuChange">
      <iMenuItem v-for="(item, i) in menuItems" :name="item.id" :key="i">
        <Icon :size="16" :type="item.icon"></Icon>{{item.name}}
      </iMenuItem>
    </iMenu>
  </div>
</template>
<script>
  import Menu from 'iview/src/components/menu'
  import Icon from 'iview/src/components/icon'
  const prefix = 'gtu-console-main-sidebar'

  export default {
    name: 'console_main_sidebar',
    props: ['width'],
    components: {
      Icon,
      iMenu: Menu,
      Submenu: Menu.Sub,
      iMenuItem: Menu.Item,
      MenuGroup: Menu.Group
    },
    data () {
      return {
        menuItems: this.getMenus()
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {
      getMenus () {
        return [
          {
            name: '数据',
            id: 'data',
            icon: 'upload'
          },
          {
            name: '底图',
            id: 'theme',
            icon: 'map'
          },
          {
            name: '保存',
            id: 'save',
            icon: 'ios-checkmark'
          },
          {
            name: '部件',
            id: 'component',
            icon: 'ios-pricetag'
          },
          {
            name: '分享',
            id: 'share',
            icon: 'android-share-alt'
          },
          {
            name: '导出',
            id: 'export',
            icon: 'ios-download-outline'
          }
        ]
      },
      handleMenuChange (name) {
        console.log(name);
        this.$emit('on-change-slider', name);
      }
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-console-main-sidebar';
  $width: 70px;
  $bg: #3E3E3E;
  #{$prefix} {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 999999;
    width: $width;
    background: $bg;
    .ivu-menu-vertical .ivu-menu-item {
      padding-left: 0;
      padding-right: 0;
      text-align: center;
    }
    .ivu-icon {
      display: block;
    }
  }
</style>


