<template>
  <div :class="classes">
    <div :class="imgClasses">
      <img :src="imgUrl" />
    </div>
    <p :class="welcomeClasses">
      您好：{{username}}！<br />
      欢迎来到集思图友地理平台！<br/>
      快去上传数据，创建您的第一个地图应用吧。
    </p>
  </div>
</template>

<script>
  import {consoleImgDir} from '@/utils/staticUrl'
  import common from '@/utils/common'
  const prefix = 'gtu-console-index'

  export default {
    name: 'console_index',
    data () {
      return {
        username: ''
      }
    },
    created () {
      /* 探测是否有cookie，即是否登录用户 */
      var account = common.getCookie('account')
      var acc = decodeURIComponent(account)
      var jsonacc = JSON.parse(acc.substring(2))
      this.username = jsonacc.user_name
    },
    computed: {
      classes () {
        return [`${prefix}`, 'gtu-container']
      },
      imgClasses () {
        return `${prefix}-img`
      },
      welcomeClasses () {
        return `${prefix}-welcome`
      },
      imgUrl () {
        return `${consoleImgDir}/index.jpeg`
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-console-index';
  #{$prefix} {
    display: flex;
    padding-top: 20px;
    align-items: center;
    &－img,
    &-welcome {
      width: 50%;
    }
    &-img {
      img {
        display: block;
      }
    }
    &-welcome {
      padding-left: 30px;
      font: {
        size: 14px;
        weight: 500;
      }
    }
  }
</style>


