<template>
  <div>
    <br>
    <iRow type="flex" justify="center" align="middle">
        <iCol span="4" offset="2">谷歌影像</iCol>
        <iCol offset="2" span="13" >
          <Slider v-model="value1"></Slider>
        </iCol>
        <iCol span="3">
          {{value1}}%
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="10" offset="2">集思图友底图</iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="10" offset="2">第三方底图</iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="10" offset="2">个性化底图</iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
         
        </iCol>
    </iRow>
    <br>
     <iRow type="flex" justify="center" align="bottom">
        <iCol span="10" offset="2">地形</iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
        <iCol span="6">
          <p :class="imgClasses">
            <img :src="src" @click="ClickToAppDetail" />
          </p>
        </iCol>
    </iRow>
  </div>
</template>

<script>
  import iRow from 'iview/src/components/Row'
  import iCol from 'iview/src/components/Col'
  import iSwitch from 'iview/src/components/Switch'
  import iProgress from 'iview/src/components/Progress'
  import iButton from 'iview/src/components/button'
  import Slider from 'iview/src/components/Slider'
  
  const prefix = 'gtu-baseMap-index'
  
  export default {
    components: {
      iRow,
      iCol,
      iSwitch,
      iProgress,
      iButton,
      Slider
    },
    name: 'baseMap-index',
    data () {
      return {
        sliderCategory: '',
        value1: 20,
        src:'/static/images/cases/cases_1.png'
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      },
      imgClasses () {
        return `${prefix}-img`
      }
    },
    methods: {
      ClickToAppDetail(){

      }

    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-baseMap-index';
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-size: 100%;

    &-img {
      overflow: hidden;
      img {
        max-width: 100%;
        height: 30px;
      }
    }

  }
</style>


