<template>
  <div :class="classes">
    <NavBar :navList="navList">
    </NavBar>
    <div class="clearfix"></div>
    <router-view></router-view>
  </div>
</template>
<script>
  const prefix = 'gtu-console'

  export default {
    name: 'console_frame',
    components: {
      NavBar: () => import(/* webpackChunkName:"common" */'@/components/navbar')
    },
    data () {
      return {
        navList: [
          {
            text: '我的数据',
            path: 'mydata'
          },
          {
            text: '我的应用',
            path: 'myapp'
          }
        ]
      }
    },
    beforeCreate () {
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-console';
  #{$prefix} {
    .ivu-menu-item-active {
      color: #fff;
      .gtu-nav-item-active-tip {
        width: 0;
        margin-left: -3px;
        border: 6px solid #FFF;
        border-top-color: transparent;
        border-left-color: transparent;
        border-right-color: transparent;
      }
    }
  }
</style>



