<template>
  <div :class="searchClasses">
    <iInput @on-click="handleSearch" v-model="keyword" icon="ios-search" placeholder="搜索"></iInput>
  </div>
</template>

<script>
  import iInput from 'iview/src/components/input'
  const prefix = 'gtu-cases-search'

  export default {
    components: {
      iInput
    },
    data () {
      return {
        keyword: ''
      }
    },
    computed: {
      searchClasses () {
        return [`${prefix}`]
      }
    },
    methods: {
      handleSearch (event) {
        this.$emit('on-search', this.keyword)
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-cases-search';
  #{$prefix} {
    position: absolute;
    right: 0;
    z-index: 100;
    .ivu-input-wrapper {
      width: 180px;
      color: #333;
      .ivu-input-icon {
        font-size: 20px;
        cursor: pointer;
      }
      .ivu-input {
        height: 36px;
        border-radius: 0;
        border-color: transparent;
        border-bottom-color: #000;
        font-size: 14px;
        &:focus {
          box-shadow: none;
        }
      }
    }
  }
</style>


