<template>
  <div style="height:600px;width: 300px;overflow: hidden;" >
    <div style="height:600px; width: 320px; overflow-y:auto; overflow-x: hidden;" >
    <br>
    <iRow type="flex" justify="center" align="bottom" >
        <iCol span="6" offset="2">打印尺寸</iCol>
        <iCol span="16">
            <iSelect v-model="model1" clearable style="width:200px" >
                <iOption v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</iOption>
            </iSelect>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="4" offset="2">方向</iCol>
        <iCol span="18">
            <RadioGroup v-model="phone">
              <iRadio label="apple">
                  <span>横向</span>
              </iRadio>
              <iRadio label="android">
                  <span>纵向</span>
              </iRadio>
            </RadioGroup>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="start" align="bottom">
        <iCol span="6" offset="2">部件选择</iCol>
    </iRow>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="8" offset="8" >
           标题
        </iCol>
        <iCol span="8" >
            <iSwitch size="large">
                <span slot="open">ON</span>
                <span slot="close">OFF</span>
            </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="8" offset="8" >
           指北针
        </iCol>
        <iCol span="8" >
            <iSwitch size="large">
                <span slot="open">ON</span>
                <span slot="close">OFF</span>
            </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="8" offset="8" >
           比例尺
        </iCol>
        <iCol span="8" >
            <iSwitch size="large">
                <span slot="open">ON</span>
                <span slot="close">OFF</span>
            </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="8" offset="8" >
           图例
        </iCol>
        <iCol span="8" >
            <iSwitch size="large">
                <span slot="open">ON</span>
                <span slot="close">OFF</span>
            </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="8" offset="8" >
           边距
        </iCol>
        <iCol span="8" >
            <iSwitch size="large">
                <span slot="open">ON</span>
                <span slot="close">OFF</span>
            </iSwitch>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="6" offset="2">左右边距</iCol>
        <iCol span="8" >
              <InputNumber :max="10" :min="1" v-model="value1"></InputNumber>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
        <iCol span="6" offset="2">上下边距</iCol>
        <iCol span="8" >
            <InputNumber :max="10" :min="1" v-model="value1"></InputNumber>
        </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="start" align="bottom">
        <iCol span="6" offset="2">预览</iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
       <iCol span="20" offset="4">
          <img src="/pub/shares/qr/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL" alt="qrcode">
       </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="center" align="bottom">
       <iCol span="20" offset="4">
          <iButton>开始生成图片</iButton>
       </iCol>
    </iRow>
    <br>
    <iRow type="flex" justify="start" align="bottom">
       <iCol span="18" offset="4">
         <p><iInput v-model="address"   icon="ios-copy-outline" readonly ></iInput></p>
       </iCol>
    </iRow>
    <br>
  </div>
 </div>
</template>

<script>
  import iRow from 'iview/src/components/Row'
  import iCol from 'iview/src/components/Col'
  import iSwitch from 'iview/src/components/Switch'
  import iSelect from 'iview/src/components/select'
  import iOption from 'iview/src/components/option'
  import iRadio from 'iview/src/components/radio'
  import InputNumber from 'iview/src/components/input-number'
  import iInput from 'iview/src/components/input'
  import iButton from 'iview/src/components/button'
  import Icon from 'iview/src/components/icon'
  

  const prefix = 'gtu-component-index'
  
  export default {
    components: {
      iRow,
      iCol,
      iSwitch,
      iOption,
      iSelect,
      iRadio,
      RadioGroup: iRadio.Group,
      InputNumber,
      iInput,
      iButton,
      Icon
    },
    name: 'component-index',
    data () {
      return {
        cityList: [
                    {
                        value: 'A4',
                        label: 'A4(cm x cm)'
                    },
                    {
                        value: 'A5',
                        label: 'A5(cm x cm)'
                    },
                    {
                        value: 'A6',
                        label: 'A6(cm x cm)'
                    }
                ],
        model1:'',
        phone:'',
        value1:0,
        address:''
      }
    },
    mounted () {
      this.address = '/apps/SqpRM8pYwB8GKzooaPyS1yRsUMpt8eBL/shares?ak=2987f7fa516c1888f710306686b44270d005e0443429067b8ae16c1076bce1b4'
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    },
    methods: {

    }

  }
</script>

<style lang="scss">
  $prefix: '.gtu-component-index';
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-size: 100%;
  }

</style>


