<template>
  <div :class="classes">
    <iTable disabled-hover :show-header="false" :columns="columns" :data="list"></iTable>
  </div>
</template>
<script>
  import iTable from 'iview/src/components/table'
  const prefix = 'gtu-console-data-list'

  export default {
    props: {
      keyword: ''
    },
    components: {
      iTable
    },
    data () {
      return {
        columns: [
          {
            title: '附件',
            key: 'attach',
            render: (h, params) => {
              return h('img', {
                    class: {
                      'gtu-console-data-list-attach': true
                    },
                    domProps: {
                      src: params.row.attach
                    }
                })
            }
          },
          {
            title: '概述',
            key: 'desc',
            render: (h, params) => {
              return h('div', [
                  h('div', params.row.name),
                  h('div', params.row.desc)
                ])
            }
          },
          {
            title: '数据类型',
            key: 'mapType'
          },
          {
            title: '数据大小',
            key: 'size'
          },
          {
            title: '创建时间',
            key: 'time'
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                  h('Icon', {
                    props: {
                      type: 'android-open',
                      size: 20
                    },
                    on: {
                      // click: () => 
                    }
                  }),
                  h('Icon', {
                    props: {
                      type: 'android-close',
                      size: 20
                    }
                  })
                ])
            }
          }
        ],
        list: this.getList()
      }
    },
    methods: {
      getList () {
        return [
          {
            attach: '/static/images/cases/cases_1.png',
            name: 'xxxxxx',
            desc: '万达影院分布情况',
            mapType: '2d',
            size: '1M',
            time: '1小时前'
          },
          {
            attach: '/static/images/cases/cases_1.png',
            name: 'xxxxxx',
            desc: '万达影院分布情况',
            mapType: '2d',
            size: '1M',
            time: '1小时前'
          }
        ]
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    }
  }
</script>

<style lang="scss">
  $prefix: '.gtu-console-data-list';
  #{$prefix} {
    margin-top: 15px;

    &-attach {
      display: block;
      width: 150px;
      height: 100%;
    }
    .ivu-table:after,
    .ivu-table:before {
      background: transparent;
    }
    .ivu-table-cell {
      padding-bottom: 15px;
      .ivu-icon {
        margin-right: 5px;
      }
    }
    .ivu-table-wrapper {
      border: 0;
    }
    .ivu-table td {
      height: auto;
      border-bottom: 0;
    }
  }
</style>


