<template>
  <div :class="classes" id="example">
    <div :class="innerClasses">
      <div :class="headClasses">
        <p>{{item.name}}</p>
        <p :class="oprClasses">
          <Icon @click.native="handleCopy" type="ios-copy"></Icon>
          <Icon @click.native="handleDelete" type="ios-trash-outline"></Icon>
        </p>
      </div>
      <div :class="contentClasses">
        <p :class="imgClasses">
          <img :src="item.img" @click="ClickToAppDetail" />
        </p>
        <p :class="descClasses">{{item.desc}}</p>
        <div :class="extraClasses">
          <p :class="timeClasses">{{item.time}}</p>
          <p :class="extraOprClasses">
            <Icon @click.native="handleView" type="eye"></Icon>
            <Icon @click.native="handleShare" type="android-share-alt"></Icon>
          </p>
        </div>
      </div>
    </div>
    <AppShare ref="modal" :obj="obj" @on-close="handleClose" v-show="showCreateModal"></AppShare>
  </div>
</template>

<script>
  import Icon from 'iview/src/components/icon'
  import AppShare from './AppShare.vue'
  const prefix = 'gtu-console-app-item'

  export default {
    props: {
      item: Object
    },
    components: {
      Icon,
      AppShare
    },
    data () {
      return {
        showCreateModal: false,
        obj: {}
      }
    },
    methods: {
      handleCopy (event) {
        this.$emit('on-copy', this.item.id)
      },
      handleDelete (event) {
        this.$emit('on-delete', this.item.id)
      },
      handleView (event) {
        alert('view' + this.item.id)
      },
      handleShare (event) {
        var params = {view_id:this.item.id}
        this.$http.get('/pub/shares/myAppShare', {params:params}).then((response) => {
          // 响应成功回调
          console.log('response:::' + response.data)
          const res = response.data
          if (res.flag) {
            this.obj = res.obj
          } 
        }, (response) => {
          // 响应错误回调
        })
        this.showCreateModal = true
        var modal = this.$refs.modal
        modal.showModal = true
        
      },
      ClickToAppDetail (event) {
        this.$router.push('main')
      },
      handleCreate (event) {
        this.showCreateModal = true
        var modal = this.$refs.modal
        modal.showModal = true
      },
      handleClose () {
        this.showCreateModal = false
      },
    },
    computed: {
      classes () {
        return `${prefix}`
      },
      innerClasses () {
        return `${prefix}-inner`
      },
      headClasses () {
        return `${prefix}-head`
      },
      oprClasses () {
        return `${prefix}-head-opr`
      },
      contentClasses () {
        return `${prefix}-content`
      },
      imgClasses () {
        return `${prefix}-img`
      },
      descClasses () {
        return `${prefix}-desc`
      },
      extraClasses () {
        return `${prefix}-extra`
      },
      timeClasses () {
        return `${prefix}-time`
      },
      extraOprClasses () {
        return `${prefix}-content-opr`
      }
    }
  }
</script>
<style lang="scss">
  $prefix : ".gtu-console-app-item";
  #{$prefix} {
    width: percentage(1/3);
    padding: 15px;
    cursor: pointer;
    .ivu-icon {
      cursor: pointer;
    }
    .ivu-icon:not(:last-child) {
      margin-right: 8px;
    }
    &-inner {
      padding: 0 10px;
      border: 1px solid #dddee1;
    }
    &-head,
    &-extra {
      display: flex;
      justify-content: space-between;
      align-items: center;
      line-height: 34px;
    }
    &-head {
      font-size: 14px;
      font-weight: 500;
    }
    &-extra {
      font-size: 12px;
    }
    &-head-opr,
    &-content-opr {
      font-size: 16px;
      color: #666;
    }
    &-img {
      overflow: hidden;
      img {
        max-width: 100%;
        height: 200px;
      }
    }
    &-desc {
      font-size: 14px;
      text-align: justify;
    }
    &-time {
      color: #999;
    }
  }
</style>


