<template>
  <div :class="classes" :style="styles" v-show="showSlider">
    <div :class="titleWrapClasses">
      <a :class="closeClasses" @click="handleClose"><Icon type="android-close"></Icon></a>
      <span :class="titleClasses">{{sliderName}}</span>
    </div>
    <div v-if="sliderName === '数据'">
       <MapData></MapData>
    </div>
    <div v-else-if="sliderName === '底图'">
      <baseMapIndex></baseMapIndex>
    </div>
    <div v-else-if="sliderName === '部件'">
      <componentIndex ></componentIndex>
    </div>
    <div v-else-if="sliderName === '分享'">
      <shareIndex></shareIndex>
    </div>
    <div v-else-if="sliderName === '导出'">
      <exportIndex></exportIndex>
    </div>
  </div>
</template>
<script>
  import Icon from 'iview/src/components/icon'
  import componentIndex from './component/index'
  import baseMapIndex from './baseMap/index'
  import exportIndex from './export/index'
  import shareIndex from './share/index'
  import MapData from './data/index'
  import Message from 'iview/src/components/message'
  import Modal from 'iview/src/components/modal'

  const prefix = 'gtu-console-main-slider'

  export default {
    props: ['right', 'sliderCategory', 'showSlider'],  // right: 距离右侧的距离；sliderCategory: menu id; showSlider:是否显示
    components: {
      Icon,
      componentIndex,
      baseMapIndex,
      exportIndex,
      shareIndex,
      MapData,
      Modal,
      Message
    },
    data () {
      return {
        sliderName: '数据',
        content:[]
      }
    },
    watch:{
      sliderCategory: function(val, oldVal){
        switch(val){
          case 'data':
            this.sliderName='数据';
            this.content = componentIndex;
            break;
          case 'theme':
            this.sliderName='底图';
            break;
          case 'save':
            this.sliderName='保存';
            this.confirm();
            break;
          case 'component':
            this.sliderName='部件';
            break;
          case 'share':
            this.sliderName='分享';
            break;
          case 'export':
            this.sliderName='导出';
            break;
        }
      }
    },
    computed: {
      classes () {
        return `${prefix}`
      },
      styles () {
        return {
          right: this.right
        }
      },
      titleWrapClasses () {
        return `${prefix}-title-wrap`
      },
      closeClasses () {
        return `${prefix}-close`
      },
      titleClasses () {
        return `${prefix}-title`
      }
    },
    methods: {
      handleClose () {
        this.$emit('on-close')
      },
      confirm () {
        this.$Modal.confirm({
            title: '保存地图应用',
            content: '<p>是否保存地图应用</p>',
            onOk: () => {
                this.$Message.info('Clicked ok');
            },
            onCancel: () => {
                this.$Message.info('Clicked cancel');
            }
        });
      },

    },
    mounted () {
      this.$Message = Message
      this.$Modal = Modal
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-console-main-slider';
  $color: #ccc;
  $border-color: #666;
  #{$prefix} {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 999;
    min-width: 300px;
    background: #4A4A4A;
    transition: all ease .3s;
    color: $color;
    &-title-wrap {
      display: flex;
      justify-content: space-between;
      line-height: 36px;
      padding: 0 20px;
      border-bottom: 1px solid $border-color;
      font-size: 14px;
    }
    &-close {
      font-size: 16px;
      color: $color;
    }
    &-title {

    }
  }
</style>


