<template>
  <div :class="classes">
    <AppItem @on-copy="handleCopy" @on-delete="handleDelete" v-for="(l, i) in list" :item="l" :key="i"></AppItem>
  </div>
</template>

<script>
  import AppItem from './AppItem.vue'
  const prefix = 'gtu-console-app-list'

  export default {
    props: {
      keyword: ''
    },
    components: {
      AppItem
    },
    data () {
      return {
        list: []
      }
    },
    methods: {
      getAppList () {
        var params = {keyword: this.keyword}
        this.$http.get('/api/myapps/app_list', {params:params}).then((response) => {
          // 响应成功回调
          const res = response.data
          if (res.flag) {
            // 根据keyword查询或显示全部
            this.list = Array(res.obj.length).fill(0).map(function (val, i) {
              var name = (res.obj[i].view_flag === 0) ? '二维应用' : '三维应用'
              return {
                id: res.obj[i].view_id,
                name: name + '-' + res.obj[i].view_name,
                img: '/static/images/cases/cases_1.png',
                desc: res.obj[i].view_des,
                time: res.obj[i].uptime
              }
            })
            // 响应条数
            this.$emit('listenTotal', res.obj.length)
          } else {
          }
        }, (response) => {
          // 响应错误回调
        })
      },
      handleDelete (id) {
        alert('delete ' + id + ' in AppList.vue')
      },
      handleCopy (id) {
        alert('copy ' + id + ' in AppList.vue')
      }
    },
    created () {
      this.getAppList()
    },
    computed: {
      classes () {
        return `${prefix}`
      }
    }
  }
</script>

<style lang="scss">
  $prefix: ".gtu-console-app-list";

  #{$prefix} {
    display: flex;
  }
</style>


