<template>
  <Modal v-model="showModal" width="300" class="gtu-share" @on-cancel="handleClose">
    <div :class="`${prefix}-barcode`">
      <img :src="obj.qrcode" alt="qrcode">
      <p>点击二维码浏览</p>
    </div>
    <my-share :class="`${prefix}-share`" :url="obj.address" ></my-share>
   <div :class="`${prefix}-url`">
      <p>分享链接</p>
      <p><iInput v-model="obj.address" readonly icon="ios-copy-outline" @on-click="copyInput" >
      </iInput></p>
    </div>
    <div slot="footer"></div>
  </Modal>
</template>

<script>
  import Modal from 'iview/src/components/modal'
  import iInput from 'iview/src/components/input'
  import iButton from 'iview/src/components/button'
  import Message from 'iview/src/components/message'
  const prefix = 'gtu-share'

  export default {
    props: {
      obj: Object
    },
    components: {
      Modal,
      iInput,
      iButton
    },
    data () {
      return {
        showModal: true,
        modalLoading: false,
        prefix: prefix,
        url: ''
      }
    },
    methods: {
      handleCreate () {
        this.modalLoading = true
        // alert('创建')
        var self = this
        setTimeout(function () {
          self.handleReset()
          self.modalLoading = false
          self.showModal = false
        }, 1000)
      },
      handleClose () {
        this.$emit('on-close')
      },
      copyInput (event) {
        var params = {ipaddress:this.obj.address}
        this.$http.get('/pub/shares/copyUrl', {params:params}).then((response) => {
          // 响应成功回调
          console.log('response:::' + response.data)
          const res = response.data
          if (res.flag) {
            this.$Message.success('已复制到粘贴板');
          }
        }, (response) => {
          // 响应错误回调
          this.$Message.error('复制到粘贴板失败');
        })
      }
    },
    mounted () {
      this.$Message = Message
    }
  }
</script>
<style lang="scss">
  $prefix: '.gtu-share';
  $modal-color: #383838;
  #{$prefix}{
    .ivu-modal {
      border: 1px solid $modal-color;
    }
    .ivu-modal-content {
      border-radius: 0;
    }
    .ivu-modal-close {
      top: 4px;
    }
    .ivu-modal-close .ivu-icon-ios-close-empty:hover {
      color: #fff;
    }
    .ivu-modal-content {
      .ivu-form-item {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
        .ivu-btn {
          padding: 3px 12px;
          color: #57a3f3;
        }
      }
    }
    .ivu-modal-footer {
      padding: 0px 16px 25px;
      border-top: 0;
      text-align: center;
      .ivu-btn {
        width: 40%;
        background: $modal-color;
        color: #fff;
      }
    }
    &-barcode {
      margin-right: 20px;
      text-align: center;
      img {
        width: 150px;
        height: 150px;
        margin-bottom: 5px;
      }
      &>p {
        text-align: center;
      }
    }
    &-share {
      text-align: center;
    }
  }
</style>


